.app {
    font-family: "Poppins", sans-serif;
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 60vw;
    background-color: #f5f5f5;
    margin: auto;
    font-size: 16px;
    height: 100vh;
    box-shadow: 0 0 10vw lightgrey;
    overflow-y: auto;
    overflow-x: hidden;
}

.app-inline {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
}

.app h1 {
    font-weight: 500;
}

.app h2 {
    font-size: 1.2rem;
    font-weight: 500;
    margin-bottom: 0;
}

.app h3 {
    font-weight: 500;
}

.app h4 {
    font-weight: 500;
}


/* ----------------------------------------
*
*
*            HEADER COMPONENT
*
*
* ---------------------------------------- */

.header {
    display: flex;
    position: relative;
    align-items: center;
    justify-content: center;
    padding-bottom: 2vh;
    box-shadow: 0 2px 4px lightgrey;
    clip-path: inset(2px 2px -2px 2px);
    margin-left: 1vw;
    margin-right: 1vw;
}

.header img {
    width: 10rem;
    margin-top: 4vh;
    border-radius: 5px;
    box-shadow: 0 2px 4px grey;
    margin-bottom: 2.2vh;
    margin-left: 1vw;
}

.header h1 {
    font-family: "Pacifico", sans-serif;
    margin-bottom: 0.5vh;
    margin-top: auto;
    margin-left: 1vw;
    padding-right: 2vw;
    font-weight: 900;
    font-size: 2.2rem;
    background: linear-gradient(to top, #dc8a78, #f2d5cf);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    filter: drop-shadow(-2px 2px 0.5px #626880);
    margin-right: auto;
}

.language {
    display: flex;
    align-items: center;
    position: absolute;
    top: 2vh;
    right: 0;
    height: 4vh;
    width: auto;
}

.language h4 {
    font-family: "Poppins", sans-serif;
    font-weight: normal;
    margin-right: 1px;
}

.language button {
    border: none;
    background-color: transparent;
    cursor: pointer;
}

/* --------------------------------------
*
*
*             MAIN COMPONENT
*
*
* -------------------------------------- */

.main {
    margin-left: 1vw;
    margin-right: 1vw;
    box-shadow: 0 2px 4px lightgrey;
    clip-path: inset(1px 1px -2px 1px);
    text-wrap: true;
    flex-grow: 1;
    overflow: auto;
}

.main h1 {
    color: #4c4f69;
    font-size: 1.6rem;
}

.main p {
    padding-bottom: 2vh;
    font-weight: 300;
}

/*     CV      */

.cv h3 {
    font-size: 1.6rem;
    font-weight: 500;
    color: #4c4f69;
}

.cv i {
    font-size: 1.6rem;
    color: #dc8a78
}

.card {
    margin-left: 1vw;
    margin-right: 1vw;
}

.card i {
    color: #8c8fa1;
    font-style: normal;
    margin-top: -1vh;
    font-size: 0.6rem;
}

.card h4 {
    text-decoration: none;
    margin-bottom: -1vh;
    margin-top: -1vh;
}

.card a {
    color: #4c4f69;
}

.card a:hover {
    color: #dc8a78;
}

.card p {
    margin-top: 1vh;
}

/*            SKILLS         */

.skills i {
    color: #dc8a78;
    margin-right: 1vw;
}

.skills h2 {
    color: #4c4f69;
    margin-bottom: -2vh;
}

.skills-div {
    display: flex;
    align-items: center;
    margin-bottom: 2.2vh;
}

.skills-list h5 {
    font-family: "Poppins", sans-serif;
    font-weight: 500;
    color: #4c4f69;
    margin-left: 2vw;
    margin-bottom: 0;
}

.skills-list ul {
    margin-top: 0;
}

.skills-list ul:li {
    font-family: "Poppins", sans-serif;
    font-weight: 500;
}

.skills-list li::marker {
    color: #dc8a78;
}

/* --------------------------------------
*
*
*            CONTACT COMPONENT
*
*
* ------------------------------------- */

.contact {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 1vw;
    color: #4c4f69;
    width: 100%;
    height: auto;
    margin-bottom: 5vh;
    margin-top: 2vh;
}

.contact--information {
    font-size: 0.6rem;
    margin-right: auto;
}

.contact--information h3 {
    margin: 0;
    font-weight: normal;
    font-size: 1rem;
    margin-top: -2vh;
}

.contact--information p {
    margin: 0;
}

.contact--links {
    display: flex;
    align-items: center;
    margin-top: 1vh;
}

.contact--links i {
    margin-right: 0.2vw;
    font-size: 1rem;
}

.contact--links p {
    margin-right: 1vw;
    font-size: 0.5rem;
}

.contact--links a {
    text-decoration: none;
    color: #4c4f69;
}

.contact--links a:hover {
    color: #dc8a78;
}

/*       DROPDOWN BUTTON          */

.dropdown--position {
    position: relative;
    margin-right: 2vw;
    margin-left: auto;
    margin-bottom: auto;
    margin-top: -2vh;
}

.dropdown {
    position: relative;
    display: inline-block;
}

.dropdown--button {
    border: none;
    background-color: transparent;
    font-size: 2rem;
    color: #4c4f69;
}

.dropdown--content {
    display: none;
    position: absolute;
    background-color: #f5f5f5;
    min-width: 10vw;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
    bottom: 0;
    right: 0;
}

.dropdown--content .unclicked-button:hover {
    background-color: #dc8a78;
    color: #F5F5F5;
}

.dropdown:hover .dropdown--content {
    display: block;
    animation: rotateY 0.5s ease;
}

.unclicked-button {
    display: block;
    background-color: transparent;
    border: none;
    width: 100%;
    cursor: pointer;
    font-family: "Poppins", sans-serif;
    color: #4c4f69;
    height: 7vh;
    text-align: left;
}

.clicked-button {
    background-color: #4c4f69;
    display: block;
    border: none;
    width: 100%;
    cursor: pointer;
    font-family: "Poppins", sans-serif;
    color: #f5f5f5;
    height: 7vh;
    text-align: left;
}

/* ---------------------------------------
*
*
*            MEDIA CHANGES 
*
*
* --------------------------------------- */

@media (max-width: 1024px) {
    .app {
        max-width: 90vw;
        height: 100vh;
        font-size: 12px;
    }
    .header h1 {
        font-size: 1.6rem;
    }
    .header img {
        margin-bottom: 1.6vh;
        width: 6rem;
    }
    .card i {
        margin-top: -0.1vh;
    }
    .card h4 {
        margin-bottom: -0.1vh;
    }
}

@keyframes fadeEffect {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

.fade {
    animation: fadeEffect 1s ease;
}

@keyframes rotateY {
     0% {
        transform: rotateY(90deg)
    }
    80% {
        transform: rotateY(-10deg)
    }
    100% {
        transform: rotateY(0)
    }
}

